/* global ghCaptcha, app */

export default class hCaptchaState {
    constructor(callback, errorCallback = null) {
        this.callback = callback;
        this.errorCallback =
            errorCallback ||
            ((alertAttrs) => {
                // By default, the alert will just be shown globally
                app.alerts.show(alertAttrs);
            });
        this.widgetId = null;
    }

    render(element) {
        this.widgetId = ghCaptcha.render(element, {
            sitekey: app.data['vbgamer45-hCaptcha.credentials.site'],
            theme: app.forum.attribute('darkMode') ? 'dark' : 'light',
            type: app.data['vbgamer45-hCaptcha.type'],
            size: app.data['vbgamer45-hCaptcha.type'] === 'invisible' ? 'invisible' : 'normal',
            callback: this.callback,
            'error-callback': () => {
                // Similarly to error.alert, we create an alert payload that can then be shown in-context depending where the code is called from
                const alertAttrs = {
                    type: 'error',
                    content: app.translator.trans('vbgamer45-hCaptcha.forum.error'),
                };
                this.errorCallback(alertAttrs);
            },
        });
    }

    getResponse() {
        return ghCaptcha.getResponse(this.widgetId);
    }

    execute() {
        return ghCaptcha.execute(this.widgetId);
    }

    reset() {
        return ghCaptcha.reset(this.widgetId);
    }
}
